
table {
   border-collapse: collapse;
   border-spacing: 0;
   width: 100%;
   border: 1px solid #ddd;
 }
 
 th, td {
   text-align: left;
   padding: 10px;
 }
 
 tr:nth-child(even){background-color: #f2f2f2}

 .container{
   padding: 20px;
   /*height: 800px;
   border: 1px dotted black;
   overflow-x: hidden;
   overflow-y: scroll;*/
 }

 ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.stcontainer{
  margin-top: 50px;
  margin-left: 140px;
  /*background-color: aqua;*/
  text-align: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr ;
  row-gap: 22px;
  column-gap: 22px;
  width:30%;
}

.smtcontainer{
  margin-top: 30px;
  margin-left: 400px;
  /*background-color: aqua;*/
  text-align: center;
  justify-self: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  row-gap: 22px;
  column-gap: 22px;
  width:30%;
}

.stateHead , .countryh{
  color: blue;
  margin-top:20px;
  font-size: 22px;
  text-align: center;
}

.item{
  background-color: cornsilk;
}

.activeCase,.confirmed,.recoveredCase,.deathsCase,.todayCases,.recoveredCases,.deathCases{
  height: 100px;
  width: 150px;
  padding: 40px;
  text-align: center;
  /*color: rgb(4, 43, 88);*/
  font-size: 20px;
}

.todayCases{
  background-color:#8FBC8F;
}

.recoveredCases{
  background-color:#FFCBA4;
}

.deathCases{
  background-color:#B7CEEC;
}

.deathsCase{
  background-color:  rgb(247, 195, 195);
}

.activeCase{
  background-color: cyan;
}

.confirmed{
  background-color:rgb(248, 232, 139);
}

.recoveredCase{
  background-color: rgb(150, 238, 150);
}

.formedit{
  /*background-color: aqua;
  text-decoration: none;*/
  text-align: center;
  padding: 20px;
  margin-top:30px;
}

.inputtext{
  padding:15px;
  width: 20%;
  border:0;
  box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
}

input[type="submit"]{
  padding:15px;
  width: 7%;
  border:0;
  color: white;
  background-color: rgb(243, 173, 82);
  box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
}

